/* eslint no-restricted-globals: 0*/
import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import registerServiceWorker from './registerServiceWorker';
import Auth from './Auth';
import axios from 'axios';
import { Router } from 'react-router-dom';

const auth =new Auth();

let state ={};
window.setState= (changes) => {
    state=Object.assign({},state,changes);
    ReactDOM.render(<Router {...state} />, document.getElementById('root'));
};

let username= auth.getProfile().name || "Jonny Williams" ;
let mail= auth.getProfile().email || "jonny.willimas@gmail.com" ;
let nickname= auth.getProfile().nickname || "jonny.williams" ;
let rol=axios.get('/User/all/role/'+mail) ||'-1';

let initialState = {
    name: username,
    email: mail,
    nickname: nickname,
    rol: rol,
    location: location.pathname.replace(/^\/?|\/$/g,""),
    auth
};


window.setState(initialState);

registerServiceWorker();


